import 'aframe';
import 'super-hands';
import { h, Component } from 'preact';
import style from './style';
import loadComponents from './components';
import CANNON from 'cannon';
import physics from 'aframe-physics-system';
import HeadsetPlayer from './components/HeadsetPlayer';
import CellphonePlayer from './components/CellphonePlayer';
import { getDisplay } from './components/helpers';
physics.registerAll();

export default class Profile extends Component {
  componentWillMount() {
    loadComponents();
    this.prepareGame();
  }

  prepareGame = () => {
    getDisplay()
      .then(isHeadSet => {
        this.setState({
          lives: 3,
          timeStart: Date.now(),
          isHeadSet,
          loser: false,
          isReady: true,
        }, () => console.log(this.state));
      })
  };

  removeLife = () => {
    this.setState(
      ({ lives }) => ({
        lives: lives - 1
      }),
      this.gameLoop
    );
  };

  gameLoop = () => {
    const { lives, timeStart } = this.state;
    const loser = lives < 0;
    const timePlaying = Date.now();
    const timeSession = loser ? timeStart - timePlaying : 0;
    this.setState({
      loser,
      timeSession
    });
  };
  componentDidMount() {
    setTimeout(() => {
      this.box.body.applyImpulse(
        new CANNON.Vec3(10, 10, 2) /* impulse */,
        new CANNON.Vec3().copy(
          this.box.getAttribute('position')
        ) /* world position */
      );
    }, 10000);
  }

  getPlayer = () => {
    const { lives, loser, isHeadSet } = this.state;
    if (isHeadSet) {
      return (
        <HeadsetPlayer
          removeLife={this.removeLife}
          lives={lives}
          loser={loser}
        />
      );
    }
    return (
      <CellphonePlayer
        lives={lives}
        winner={!loser}
      />
    );
  };

  render() {
    // const debug = process.env.NODE_ENV === 'development' ? 'debug: true' : '';
    const { isReady } = this.state;
    if (!isReady) {
      return null;
    }
    const player = this.getPlayer();
  
    return (
      <a-scene physics="friction: 0.2; restitution: 1; gravity: -0.5; debug: true;">
        <a-assets>
          <img
            id="skyTexture"
            src="https://cdn.aframe.io/a-painter/images/sky.jpg"
          />
          <img
            id="groundTexture"
            src="https://cdn.aframe.io/a-painter/images/floor.jpg"
          />
          <a-mixin
            id="controller"
            super-hands
            sphere-collider="objects: .cube, .transformer"
            static-body="shape: sphere; sphereRadius: 0.02;"
          />
          <a-mixin id="cube-close" material="opacity: 0.7; transparent: true" />
          <a-mixin
            id="cube"
            geometry="primitive: box; width: 0.33; height: 0.33; depth: 0.33"
            hoverable
            grabbable
            drag-droppable
            dynamic-body
          />
        </a-assets>
        <a-entity
          geometry="primitive: box; depth: 0.5; height: 0.5; width 0.5"
          material="shader: standard"
          position="0 0.5 -2"
          random-color
        />
        <a-cylinder
          static-body
          id="ground"
          src="https://cdn.aframe.io/a-painter/images/floor.jpg"
          radius="32"
          height="0.1"
        />

        <a-box static-body position="0 2 -5" width="3" height="1" depth="1" />
        <a-sphere
          grabbable
          maxGrabbers
          ref={c => {
            this.box = c;
          }}
          dynamic-body
          position="0.5 10 0"
          width="1"
          height="1"
          depth="1"
        />

        <a-sky
          id="background"
          src="#skyTexture"
          theta-length="90"
          radius="30"
        />
        { player }
      </a-scene>
    );
  }
}



